<template>
  <div>
    <van-nav-bar title="设置" class="backgroundNaviBar" z-index="999" :border="false">
    </van-nav-bar>
    <!-- <div class="headerImage">
      <div class="img">
        <img
          :src="$store.state.headerImage"
          class="imgs"
          alt
          v-show="$store.state.headerImage!==''"
        />
        <img
          src="static/img/headerImage.png"
          @click="cameraTakebtn"
          alt
          v-show="$store.state.headerImage===''"
        />
      </div>
      <div class="title">
        <span>Hi,</span>
        <span>{{user}}</span>
      </div>
      <div class="imgRight">
        <mu-icon color="grey" size="24" value="keyboard_arrow_right"></mu-icon>
      </div>
    </div> -->
    <div class="headerSetting backgroundNaviBar">
       <div class="borderCicle">
         <img src="static/img/headerImage.png" alt="">
       </div>
       <div>
         <span>嗨！超级管理员</span>
       </div>
    </div>
    <div style="padding:10px 0;">
      <div class="backStyle">
        <div v-for="(item,index) in itemList" :key="index" @click="navigationTo(item.name)">
          <div class="flexBox BoxStyle" v-if="item.status">
            <div>{{item.name}}</div>
            <div class="imageSize">
              <mu-icon color="grey" size="24" value="keyboard_arrow_right"></mu-icon>
            </div>
          </div>
        </div>
      </div>
      <div class="bottomSubmit">
        <div class="btnSubmit backgroundNaviBar" @click="changeUser()">
          <span>退出用户</span>
        </div>
      </div>
      <van-dialog
        v-model="openSimple"
        :show-cancel-button="showVersionUpdate"
        @confirm="confirmUpdate"
      >
        <div class="updataHeader">
          <span>版本更新</span>
        </div>
        <div class="boxUpdata">
          <div v-if="showVersionUpdate">
            <div class="heightStyle">
              <span>更新的版本：</span>
              <span>{{versionData.version}}</span>
            </div>
            <span class="heightStyle" style="height:30px;">
              <span>版本解决了以下的问题：</span>
            </span>
            <div v-for="(item,index) in versionData.description" :key="index">
              <div class="heightStyle">
                <span>{{index+1}}、</span>
                <span>{{item}}</span>
              </div>
            </div>
          </div>
          <div v-if="!showVersionUpdate">
            <div class="heightStyle">
              <span>当前为最新版本：</span>
              <span style="color:rgb(50, 142, 251)">{{version}}</span>
            </div>
          </div>
        </div>
      </van-dialog>
    </div>
    <!-- <div @click="cameraTakebtn">
      <span>调用相机</span>
    </div>
    <div v-for="(item,index) in $store.state.cameraPic" :key="index">
        <img :src="item" alt="" srcset="">
    </div>-->
    <van-action-sheet
      style="z-index:999"
      v-model="showApp"
      :actions="actions"
      cancel-text="取消"
      @cancel="onCancel"
      @select="onSelect"
      description="选择市场安装，请确保已经安装安智市场"
    />
  </div>
</template>
<script>
var token = sessionStorage.getItem("token");
import Setting from "../../../static/setting/index.js";
import CameraTake from "../../../static/js/cordovaCamare/camera";
import AppUpdata from "../../../static/js/anzhiShop/index";
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { Dialog, Toast } from "vant";
import { ActionSheet } from "vant";
Vue.use(ActionSheet);

Vue.use(Toast);
export default {
  data() {
    // "推送设置", "清理缓存", "联系客服", "关于我们", "版本信息","用户审核"
    return {
      differrent:false,
      itemList: [],
      msg: "hello 数据详情",
      open: "send",
      openSimple: false,
      user: "",
      picTure: [],
      version: localStorage.getItem("version"),
      showVersionUpdate: false,
      versionData: [],
      appName: "",
      actions: [{ name: "市场更新" }, { name: "本地更新" }],
      showApp: false
    };
  },
  methods: {
    onCancel() {
      this.showApp = false;
      // Toast('cancel');
    },
    onSelect(item) {
      if (item.name == "市场更新") {
        AppUpdata.updataApp();
      } else {
        var http = this.appName;
        // var http = "8886/group1/M00/00/01/rBLi414YOk-ACLArASIf1vSN0Rk764.apk";
        window.location.href = "http://47.112.16.168:" + http;
      }
    },
    confirmUpdate() {
     if (this.differrent) {
        this.showApp = true;
     } else {
       this.showApp = false;
     }
      // 去到应用市场
      // AppUpdata.updataApp();
      // var http = this.versionData.path;
      // // var http = "8886/group1/M00/00/01/rBLi414YOk-ACLArASIf1vSN0Rk764.apk";
      // window.location.href = "http://47.112.16.168:" + http;
    },
    cameraTakebtn() {
      CameraTake.cameraTake();
    },
    Exit() {
      // this.$router.replace("/Login");
      localStorage.clear();
      sessionStorage.clear();
      // this.$parent.WebSocketClose();
      navigator.app.exitApp();
      // window.location.reload();
      // localStorage.setItem("islogin","");
    },
    navigationTo(e) {
      // console.log(e);
      switch (e) {
        case "关于我们":
          this.$router.push("/aboutMe");
          break;
        case "联系客服":
          this.callPhone();
          break;
        case "清理缓存":
          this.clearCache();
          break;
        case "版本信息":
          this.openSimpleDialog();
          break;
        case "用户审核":
          // this.openSimpleDialog();
          this.Toexamine();
          break;
        case "用户反馈":
          // alert("服务器正在升级中。。。");
          this.$router.push("/Feedback");
          break;
        case "标准值设定":
          // alert("服务器正在升级中。。。");
          this.$router.push("StandardSetting");
          break;
        case "退出登录":
          // alert("服务器正在升级中。。。");
          this.changeUser();
          break;
        case "转运目的地录入":
          this.$router.push("/TransshipmentPurpose");
          break;
        // case "注销登录":
        //   // alert("服务器正在升级中。。。");
        //   this.changeUser();
        //   break;
        // 反馈列表
        default:
          // news();
          this.$toast.warning("测试升级中");
          break;
      }
    },
    callPhone() {
      window.location.href = "tel://0755-22672445";
    },
    clearCache() {
      localStorage.clear();
      const loading = this.$loading();
      setTimeout(() => {
        loading.close();
      }, 2000);
    },
    Toexamine() {
      this.$router.push("/Toexamine");
    },
    async openSimpleDialog() {
      var _this = this;
      if (
        localStorage.getItem("version") === null ||
        localStorage.getItem("version") === undefined ||
        localStorage.getItem("version") === ""
      ) {
        cordova.getAppVersion.getVersionNumber().then(function(version) {
          //获取当前app的版本号；
          // $scope.now_version = version;
          localStorage.setItem("version", version);
        });
      }
      if (this.$store.state.upDateStatus === false) {
        var token = sessionStorage.getItem("token");
        var url = "/table/appVersion";
        var res = await AppBace.request(url, "get", {}, token);
        var versionApp = localStorage.getItem("version");
        this.version = versionApp;
       console.log(res);
        if (
          res.data.data !== null &&
          res.data.data !== undefined &&
          res.data.data !== ""&&
          res.data.data.length>0
        ) {
          var arr = res.data.data.version.split(".");
          var arr1 = versionApp.split(".");
          var currentVersion = arr1[0] + arr1[1] + arr1[2];
          var serviceVersion = arr[0] + arr[1] + arr[2];
          console.log(serviceVersion);
          if (Number(serviceVersion) > Number(currentVersion)) {
            _this.$store.state.appVersion = res.data.data.version;
            _this.differrent = true;
            // 更新提示
            _this.showUpdataApp = true;
            _this.showVersionUpdate = true;
            _this.$store.state.uadataContent = res.data.data;
            _this.versionData = res.data.data;
            _this.appName = res.data.data.path;
          }
        } else {
          // Toast.fail("获取服务器版本失败！");
          _this.differrent = false;
        }
      }

      this.openSimple = true;
      this.versionData = this.$store.state.uadataContent;
      // var _this = this;
      var http = "8886/group1/M00/00/01/rBLi414YOk-ACLArASIf1vSN0Rk764.apk";
      if (this.$store.state.appVersion !== 0) {
        this.showVersionUpdate = true;
      } else {
        this.showVersionUpdate = false;
      }
    },
    closeSimpleDialog() {
      this.openSimple = false;
    },
    changeUser() {
      var _this = this;
      Dialog.confirm({
        title: "注销",
        message: "是否退出当前用户？"
      })
        .then(() => {
          // on confirm
          _this.$store.state.mapStatus = false;
          _this.$router.replace("/");
          localStorage.clear();
          sessionStorage.clear();
          _this.$store.state.tabBarPage = 0;
        })
        .catch(() => {
          // on cancel
        });
    },
    forRole(e) {
      var name = "";
      switch (e) {
        case "operator":
          name = "运维人员";
          break;

        case "station":
          name = "水站管理员";
          break;
        case "manager":
          name = "环保管理员";
          break;
        default:
          name = "超级管理员";
          break;
      }
      return name;
    }
  },
  created() {},
  mounted() {
    var user = sessionStorage.getItem("user");

    // this.itemList = Setting.initMenu(user);
    var arr = Setting.initMenu(user);
    this.itemList = arr;
    this.user = this.forRole(user);
  }
};
</script>
<style scoped>
.headerSetting{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 30px 0;
}
.borderCicle{
  border-radius: 60px;
  width: 120px;
  height: 120px;
  background-color: #b2daff;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5px;
}
.borderCicle img{
  width: 80px;
  height: 80px;
}
.div {
  /* border: #ccc 1px solid; */
  width: 100%;
  border-bottom: #ccc 1px solid;
  font-size: 14px;
  line-height: 30px;
  height: 30px;
  /* padding: 10px; */
}
.list {
  padding-top: 40px;
}
.demo-list-wrap {
  width: 100%;

  border-radius: 10px;
  background: white;
}
.list1 {
  width: 96%;
  background: white;
}
.btn {
  width: 100%;
  margin-top: 40px;
}
.headerImage img {
  width: 50px;
  height: 50px;
}
.headerImage {
  height: 100px;
  /* width: 100%; */
  border-radius: 5px;
  background: white;
  display: flex;
  flex-direction: row;
  padding: 10px;
  margin: 0 10px;
  margin-top: 10px;
  padding-right: 20px;
}
.title {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
}
.imgRight img {
  width: 10px;
  height: 10px;
}
.imgRight {
  display: flex;
  align-items: center;
}
.img {
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(245, 245, 245);
  border-radius: 50%;
}
.imgs {
  transform: rotate(90deg);
}
.btnSubmit {
  height: 40px;
  color: white;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 96%;
  margin-top: 40px;
}
.bottomSubmit {
  display: flex;
  justify-content: center;
}
.heightStyle {
  height: 40px;
  display: flex;
  align-items: center;
  font-size: 14px;
  padding: 0 10px;
}
.boxUpdata {
  padding: 10px;
}
.updataHeader {
  height: 40px;
  border-bottom: 2px rgb(50, 142, 251) solid;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(50, 142, 251);
}
.imageSize img {
  width: 16px;
  height: 16px;
}
.flexBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}
.BoxStyle {
  height: 50px;
  border-bottom: 1px solid rgb(240, 240, 240);
}
.backStyle {
  background-color: white;
  border-radius: 5px;
}
</style>



